.header{
    background-color: #131921;
    display: flex;
    align-items:center;
    position: sticky;
    z-index: 100;
}
.header__logo{
    width: 50px;
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header__optionline1{
    font-size: 10px;
}
.header__optionline2{
    font-size: 13px;
    font-weight: 800;
}
.header__link{
    color: white;
    text-decoration: none;
}
.header__search{
    display: flex;
    flex: 1;
}
.header__optionBasket{
    display: flex;
    align-items: center;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__searchInput{
    height: 12px;
    padding:10px;
    width: 100%;
    border: none;
}
.header__searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}